import {useParams} from 'react-router-dom';
import {SocialLinks} from '../data/data';
import Contact from './contact';

export default () => {
  const {ref} = useParams();
  const links = Object.entries(SocialLinks[ref] || SocialLinks['default']);
  return (
      <ul className="contacts__list">
        {links.map(([key, value]) => {
          return Contact({
            className: key,
            text: value.text,
            link: value.link,
          });
        })}
      </ul>
  )
}

