import React from "react";
import {
  //  github pages  HashRouter .. gh ,        
  HashRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import Auth from './components/auth';

export default function App() {
  return (
      <Router hashType="noslash">
        <div className="container">
          <Switch>
            <Route path="/:ref">
              <Auth />
            </Route>
            <Route path="/">
              <Auth />
            </Route>
          </Switch>
        </div>
      </Router>
  );
}
